import { createRouter, createWebHistory } from 'vue-router'

import HomePage from '../views/HomePage.vue'
import CatalogPage from '../views/CatalogPage.vue'
import AdminPanel from '../views/AdminPanel.vue'  // Админская панель
import AdminOrders from '../components/AdminOrders.vue' // Страница заказов

// Контактная страница импортируется динамически, как и остальные
const ContactPage = () => import('../views/ContactPage.vue')
const IphonesCategory = () => import('../views/IphonesCategory.vue')
const IpadsCategory = () => import('../views/IpadsCategory.vue')
const AccessoriesCategory = () => import('../views/AccessoriesCategory.vue')
const CartPage = () => import('../views/CartPage.vue')

const routes = [
  {
    path: '/',
    name: 'Home',
    component: HomePage
  },
  {
    path: '/catalog',
    name: 'Catalog',
    component: CatalogPage
  },
  {
    path: '/catalog/iphones',
    name: 'Iphones',
    component: IphonesCategory
  },
  {
    path: '/catalog/ipads',
    name: 'Ipads',
    component: IpadsCategory
  },
  {
    path: '/catalog/accessories',
    name: 'Accessories',
    component: AccessoriesCategory
  },
  {
    path: '/cart',
    name: 'Cart',
    component: CartPage
  },
  {
    path: '/contact',
    name: 'Contact',
    component: ContactPage
  },
  {
    path: '/admin',
    name: 'AdminPanel',
    component: AdminPanel,
    children: [
      {
        path: 'orders',
        name: 'AdminOrders',
        component: AdminOrders
      },
      {
        path: 'products',
        name: 'AdminProducts',
        component: () => import('../views/AdminProducts.vue')
      }
    ]
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router
