<template>
  <div class="catalog">
    <h1>Каталог товаров</h1>
    <a-row gutter="24">
      <a-col :span="8" v-for="product in products" :key="product.id">
        <a-card :title="product.name" :hoverable="true" class="product-card">
          <img :src="product.image" alt="image" class="product-image" />
          <p>{{ product.description }}</p>
          <strong>{{ product.price }} ₽</strong>
          <div class="card-footer">
            <a-button type="primary" @click="addToCart(product)">В корзину</a-button>
          </div>
        </a-card>
      </a-col>
    </a-row>
  </div>
</template>

<script>
export default {
  name: 'CatalogPage',
  data() {
    return {
      products: [
        {
          id: 1,
          name: 'Samsung Galaxy S25+ (Mint)',
          price: 70600,
          image: 'https://via.placeholder.com/200x150?text=S25%2B+Mint',
          description: 'Новинка с Snapdragon 8 Elite.'
        },
        {
          id: 2,
          name: 'Samsung Galaxy S25+ (Silver)',
          price: 70600,
          image: 'https://via.placeholder.com/200x150?text=S25%2B+Silver',
          description: 'Топовый смартфон в новом дизайне.'
        },
        {
          id: 3,
          name: 'Samsung Galaxy S25 Ultra (Black)',
          price: 91600,
          image: 'https://via.placeholder.com/200x150?text=S25+Ultra+Black',
          description: 'Флагманская модель с улучшенной камерой.'
        }
      ]
    }
  },
  methods: {
    addToCart(product) {
      // Получаем корзину из localStorage или создаём пустой массив
      let cart = JSON.parse(localStorage.getItem('cart')) || []

      // Проверяем, есть ли товар в корзине
      const exists = cart.some(item => item.id === product.id)

      if (!exists) {
        // Если товара нет в корзине, добавляем его
        cart.push(product)
        localStorage.setItem('cart', JSON.stringify(cart))  // Обновляем localStorage
        this.$message.success(`Товар добавлен в корзину: ${product.name}`)
      } else {
        // Если товар уже есть в корзине
        this.$message.warning('Товар уже в корзине')
      }
    }
  }
}
</script>

<style scoped>
.catalog {
  padding: 40px;
}

.product-card {
  margin-bottom: 24px;
}

.product-image {
  width: 100%;
  height: auto;
  margin-bottom: 16px;
}

.card-footer {
  margin-top: 12px;
  text-align: right;
}
</style>
