<template>
  <div class="admin-orders">
    <h1>Управление заказами</h1>
    <a-table :columns="columns" :data-source="orders" rowKey="id" bordered>
      <template #bodyCell="{ column, record }">
        <template v-if="column.key === 'status'">
          <a-select v-model:value="record.status" @change="updateStatus(record.id, $event)">
            <a-select-option value="В обработке">В обработке</a-select-option>
            <a-select-option value="Доставлен">Доставлен</a-select-option>
          </a-select>
        </template>
        <template v-else-if="column.key === 'actions'">
          <a-space>
            <a-button type="link" @click="editOrder(record)">Редактировать</a-button>
            <a-button type="link" danger @click="deleteOrder(record.id)">Удалить</a-button>
          </a-space>
        </template>
      </template>
    </a-table>
  </div>
</template>

<script setup>
import { ref } from 'vue'

const orders = ref([
  { id: 1, customer: 'Иванов И.И.', status: 'В обработке', total: 1200 },
  { id: 2, customer: 'Петров П.П.', status: 'Доставлен', total: 1500 }
])

const columns = [
  { title: 'Заказчик', dataIndex: 'customer', key: 'customer' },
  { title: 'Статус', dataIndex: 'status', key: 'status' },
  { title: 'Сумма', dataIndex: 'total', key: 'total' },
  { title: 'Действия', key: 'actions' }
]

const updateStatus = (id, status) => {
  const order = orders.value.find(order => order.id === id)
  if (order) {
    order.status = status
  }
}

const editOrder = (order) => {
  console.log('Редактирование заказа', order)
}

const deleteOrder = (id) => {
  orders.value = orders.value.filter(order => order.id !== id)
}
</script>

<style scoped>
.admin-orders {
  padding: 20px;
}
</style>
