<template>
  <div class="home-page">
    <BannerSlider />

    <div class="new-arrivals-section">
      <h2>Новое поступление</h2>
      <a-row gutter="24">
        <a-col :span="8" v-for="product in newArrivals" :key="product.id">
          <a-card :title="product.name" :hoverable="true" class="product-card">
            <img :src="product.image" alt="image" class="product-image" />
            <p>{{ product.description }}</p>
            <strong>{{ product.price }} ₽</strong>
            <div class="card-footer">
              <a-button type="primary" @click="addToCart(product)">В корзину</a-button>
            </div>
          </a-card>
        </a-col>
      </a-row>

      <!-- Черточка после секции новинок -->
      <div class="divider"></div>

      <div class="info-cards">
        <div class="info-card" v-for="(info, idx) in infoCards" :key="idx">
          <img :src="info.icon" :alt="info.title" />
          <h3>{{ info.title }}</h3>
          <p>{{ info.text }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BannerSlider from '@/components/BannerSlider.vue'

export default {
  name: 'HomePage',
  components: {
    BannerSlider
  },
  data() {
    return {
      newArrivals: [
        {
          id: 10,
          name: 'Apple iPhone 16 Pro Max',
          price: 106490,
          image: require('@/assets/iphone16.jpg'),
          description: 'Apple iPhone 16 Pro Max 256Gb Black Titanium'
        },
        {
          id: 11,
          name: 'Стилус Apple Pencil USB-C (3rd Generation)',
          price: 8990,
          image: require('@/assets/pencil.jpg'),
          description: 'Apple Pencil (USB-C) подключается и заряжается с помощью зарядного кабеля USB-C'
        },
        {
          id: 12,
          name: 'Ноутбук Apple MacBook Air 13 2025',
          price: 98990,
          image: require('@/assets/mak.jpg'),
          description: 'Ноутбук Apple MacBook Air 13 2025 (M4/10C CPU/8C GPU16 ГБ/256 ГБ) MW123'
        }
      ],
      infoCards: [
        {
          icon: 'https://cdn-icons-png.flaticon.com/512/1029/1029183.png',
          title: 'Доставка по России',
          text: 'Курьером и через СДЭК — по всей стране.'
        },
        {
          icon: 'https://cdn-icons-png.flaticon.com/512/535/535137.png',
          title: 'Только оригинал',
          text: 'Продаём только официальную продукцию Apple.'
        },
        {
          icon: 'https://cdn-icons-png.flaticon.com/512/1250/1250615.png',
          title: 'Гарантия',
          text: 'Официальная гарантия и возврат по закону РФ.'
        }
      ]
    }
  },
  methods: {
    addToCart(product) {
      // Получаем корзину из localStorage или создаём пустую
      const cart = JSON.parse(localStorage.getItem('cart')) || []

      // Проверяем, есть ли уже товар в корзине
      const exists = cart.find(item => item.id === product.id)

      if (!exists) {
        cart.push(product)  // Добавляем товар в корзину
        localStorage.setItem('cart', JSON.stringify(cart))  // Сохраняем корзину в localStorage
        this.$message.success(`Добавлен в корзину: ${product.name}`)  // Используем правильные обратные кавычки
      } else {
        this.$message.warning('Товар уже в корзине')
      }
    }
  }
}
</script>

<style scoped>
.home-page {
  padding: 40px;
  background-color: #f5f5f5;
}

.new-arrivals-section h2 {
  font-size: 28px;
  margin-bottom: 24px;
  text-align: center;
}

.product-card {
  margin-bottom: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;  /* Убедимся, что карточка растягивается по вертикали */
  height: 600px;  /* Фиксированная высота карточки */
}

.product-image {
  width: 100%;
  height: auto;
  max-height: 350px;  /* Максимальная высота изображения */
  object-fit: contain;  /* Изображение не будет растягиваться, но сохранит пропорции */
  margin-bottom: 16px;
}

.card-footer {
  margin-top: 12px;
  text-align: right;
}

.divider {
  margin-top: 40px;
  margin-bottom: 40px;
  border-top: 2px solid #ddd;  /* Линия */
}

.info-cards {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 40px;
}

.info-card {
  background-color: #fff;
  border-radius: 12px;
  padding: 20px;
  width: 300px;
  margin: 10px;
  text-align: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.info-card img {
  width: 64px;
  height: 64px;
  margin-bottom: 12px;
}

.info-card h3 {
  font-size: 18px;
  margin-bottom: 8px;
}

.info-card p {
  font-size: 14px;
  color: #666;
}
</style>
