import { createStore } from 'vuex';

const store = createStore({
  state: {
    // Массив с товарами
    products: [
      { id: 1, name: 'iPhone 14', category: 'iphone', price: 999, image: 'iphone14.jpg' },
      { id: 2, name: 'iPhone 13', category: 'iphone', price: 799, image: 'iphone13.jpg' },
      { id: 3, name: 'iPad Pro', category: 'ipad', price: 1099, image: 'ipadpro.jpg' },
      { id: 4, name: 'iPad Air', category: 'ipad', price: 599, image: 'ipadair.jpg' },
      { id: 5, name: 'AirPods', category: 'accessories', price: 199, image: 'airpods.jpg' },
      { id: 6, name: 'Apple Watch', category: 'accessories', price: 399, image: 'applewatch.jpg' }
    ],
    // Массив с товарами в корзине
    cart: [],
    // Массив с проданными товарами для аналитики
    soldProducts: []
  },
  getters: {
    // Получить все товары
    getProducts(state) {
      return state.products;
    },
    // Получить товары по категории
    getProductsByCategory: (state) => (category) => {
      return state.products.filter(product => product.category === category);
    },
    // Получить товары в корзине
    getCart(state) {
      return state.cart;
    },
    // Получить количество товаров в корзине
    cartItemCount(state) {
      return state.cart.length;
    },
    // Общая сумма в корзине
    totalCartPrice(state) {
      return state.cart.reduce((sum, item) => sum + item.price, 0);
    }
  },
  mutations: {
    // Добавить товар в корзину
    addToCart(state, product) {
      state.cart.push(product);
    },
    // Удалить товар из корзины
    removeFromCart(state, productId) {
      state.cart = state.cart.filter(item => item.id !== productId);
    },
    // Отметить товар как проданный
    markAsSold(state, product) {
      state.soldProducts.push(product);
    }
  },
  actions: {
    // Добавить товар в корзину и отметить его как проданный
    addProductToCart({ commit }, product) {
      commit('addToCart', product);
      commit('markAsSold', product);
    }
  }
});

export default store;